<template>
    <table class="w-full text-xs text-left text-gray-800">
        <thead class="text-xs text-gray-700 uppercase bg-gray-100">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Razón social
                </th>
                <th scope="col" class="px-6 py-3">
                    Nombre
                </th>
                <th scope="col" class="px-6 py-3">
                    Código de empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Fecha creación
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listProveedores.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="5">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr v-else v-for="proveedor in listProveedores" :key="proveedor.id"
                class="bg-white border-b hover:bg-gray-50 ease-in duration-300">
                <td class="px-6 py-3">
                    {{ proveedor.razsocial }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ proveedor.nombre }}
                </th>
                <td class="px-6 py-3">
                    {{ proveedor.codempresa }}
                </td>
                <td class="px-6 py-3">
                    {{ formatCreatedAt(proveedor.fecha_creacion) }}
                </td>
                <td class="px-6 py-2 flex gap-2">
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-green-500 hover:bg-green-600 ease-in duration-300 text-white"
                        @click.prevent="editarItem(proveedor.id)">Editar</button>
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-red-500 hover:bg-red-600 ease-in duration-300 text-white "
                        @click.prevent="eliminarItem(proveedor.id)">Eliminar</button>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableProveedores",
    props: {
        listProveedores: Array
    },
    methods: {
        eliminarItem(id) {
            this.$emit('eliminar-item', id)
        },
        editarItem(id) {
            this.$emit('editar-item', id)
        },
        formatCreatedAt(dateString) {
            const options = { year: 'numeric', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit' };
            const createdAt = new Date(dateString).toLocaleDateString('es-PE', options);
            return createdAt;
        }
    }
})
</script>
