<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear proveedores</h3>
                <ItemForm @value-input="saveNombre" label="Nombre" name="name" placeholder="Inserte el nombre" value=""/>
                <ItemForm @value-input="saveRazSocial" label="Razón social" name="razsocial" placeholder="Inserte la razón social" value=""/>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Crear</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import {ref} from 'vue';
import ItemForm from '../generales/ItemForm.vue';
export default {
    name: 'ModalCreate',
    components: {ItemForm},
    emits: ['close-modal', 'save-datos'],
    setup(_, { emit }) {
        const razSocial = ref('');
        const nombre = ref('');
        const closeModal = () =>{
            emit('close-modal')
        }
        const saveRazSocial = (value) =>{
            razSocial.value = value;
        }
        const saveNombre = (value) =>{
            nombre.value = value;
        }
        const saveDatos = () => {
            emit('save-datos',{
                razSocial : razSocial.value,
                nombre : nombre.value
            })
        }
        return {
            closeModal,
            saveRazSocial,
            saveNombre,
            saveDatos
        }
    },
};
</script>
