<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar proveedor</h3>
                <ItemForm @value-input="saveRazsocial" label="Razón social" name="razsocial"
                    placeholder="Inserte la razón social" :value="razSocial" />
                    <ItemForm @value-input="saveNombre" label="Nombre" name="nombre"
                    placeholder="Inserte el nombre" :value="nombre" />
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Actualizar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import { buscarProveedores } from '../../services/ProveedoresService';
import ItemForm from '../generales/ItemForm.vue';
export default {
    name: 'ModalEditar',
    components: { ItemForm },
    props: {
        idItem: String
    },
    emits: ['cancelar-editar', 'aceptar-editar'],
    setup(props, { emit }) {
        const razSocial = ref('');
        const nombre = ref('');

        const closeModal = () => {
            emit('cancelar-editar')
        }
        const saveRazsocial = (value) => {
            razSocial.value = value;
        }
        const saveNombre = (value) => {
            nombre.value = value;
        }
        const saveDatos = () => {
            emit('aceptar-editar', {
                id: props.idItem,
                razSocial: razSocial.value,
                nombre: nombre.value
            })
        }
        onMounted(() => {
            buscarProveedores(props.idItem)
                .then((response) => {
                    razSocial.value = response.data.data.razsocial
                    nombre.value = response.data.data.nombre
                })
        })
        return {
            razSocial,
            nombre,
            closeModal,
            saveNombre,
            saveRazsocial,
            saveDatos
        }
    },
};
</script>

