<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar proveedores</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear nuevo
            </button>
        </div>
        <div class="overflow-x-auto shadow-md sm:rounded-lg">
            <TableProveedores :listProveedores="listProveedores" @eliminar-item="onEliminarItem"
                @editar-item="onEditarItem" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="saveDatos" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar esta unidad?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idItem="idProveedor" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModaError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
<script>
/* eslint-disable */
import { ref, onMounted } from 'vue';
import { listarProveedores, eliminarProveedores, editarProveedores, crearProveedores } from '../services/ProveedoresService';
import TableProveedores from '../components/proveedores/TableProveedores';
import ModalCreate from '../components/proveedores/ModalCreate';
import ModalDelete from '../components/generales/ModalDelete.vue';
import ModalEditar from '../components/proveedores/ModalEditar';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModaError from '../components/generales/ModalError.vue';
import store from '../store'

export default ({
    name: "AdminFacturacion",
    components: {
        TableProveedores, ModalCreate,
        ModalDelete, ModalEditar,
        ModaError, ConfirmEdit
    },
    setup() {
        const openModalV = ref(false);
        const openModalEdit = ref(false);
        const openModalError = ref(false);
        const confirmDelete = ref(false);
        const openSuccessEdit = ref(false);
        const listProveedores = ref([]);
        const idProveedor = ref('')
        const openModal = () => {
            openModalV.value = true;
        };

        const closeModal = () => {
            openModalV.value = false;
        }

        const closeSuccessEdit = () => {
            openSuccessEdit.value = false;
            window.location.reload();
        }

        const onEliminarItem = (id) => {
            idProveedor.value = id
            confirmDelete.value = true;
        }

        const aceptarEliminar = () => {
            eliminarProveedores(idProveedor.value)
                .then(response => {
                    console.log(response.data)
                    confirmDelete.value = false;
                    idProveedor.value = '';
                    window.location.reload();
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        const cancelarEliminar = () => {
            confirmDelete.value = false;
        }

        const cancelarEditar = () => {
            openModalEdit.value = false;
        }
        const onEditarItem = (id) => {
            idProveedor.value = id;
            openModalEdit.value = true;
        }

        const aceptarEditar = (dataformulario) => {
            const razSocial = dataformulario.razSocial;
            const nombre = dataformulario.nombre;
            const id = dataformulario.id;
            if (razSocial == '' && nombre == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarProveedores(razSocial, nombre, id)
                    .then(response => {
                        openModalEdit.value = false;
                        openSuccessEdit.value = true;
                    })
            }
        }

        const closeModalError = () => {
            openModalError.value = false;
        }

        const saveDatos = (data) => {
            if (data.razSocial == '' || data.nombre == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearProveedores(data.razSocial, data.nombre, store.state.codempresa)
                    .then(response => {
                        openModalV.value = false;
                        window.location.reload();
                    })
            }
        }
        onMounted(() => {
            listarProveedores(store.state.codempresa)
                .then(response => {
                    listProveedores.value = response.data.data
                })
        })
        return {
            listProveedores,
            openModal,
            openModalV,
            openModalError,
            openSuccessEdit,
            closeModal,
            closeSuccessEdit,
            closeModalError,
            saveDatos,
            confirmDelete,
            onEliminarItem,
            aceptarEliminar,
            cancelarEliminar,
            cancelarEditar,
            onEditarItem,
            openModalEdit,
            idProveedor,
            aceptarEditar
        }
    }
})
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>
